<template>
<v-app id="app"
       toolbar>
  <v-navigation-drawer app
                       :clipped="mini"
                       dark
                       :mini-variant.sync="mini"
                       :mobile-break-point="getMobileBreakPoint"
                       :persistent="getViewport.x > getMobileBreakPoint"
                       :temporary="getViewport.x <= getMobileBreakPoint"
                       v-model="drawer"
                       overflow>
    <v-list>
      <v-list-tile avatar>
        <v-list-tile-avatar style="cursor: pointer">
          <v-icon large
                  class="white--text">account_circle</v-icon>
        </v-list-tile-avatar>
        <v-list-tile-content>
          <v-list-tile-title>
            {{ username | capitalize }}
          </v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-btn icon
                 @click.stop="setAsyncMini(!getMini)">
            <v-icon>chevron_left</v-icon>
          </v-btn>
        </v-list-tile-action>
      </v-list-tile>
    </v-list>
    <v-list class="pt-0"
            dense>
      <v-divider></v-divider>
      <v-list-group v-for="(item, i) in sideBarItems"
                    :key="i">
        <v-list-tile slot="item"
                     @click="item.path === 'dashboard' ? setAsyncActivePage({name: item.path, params:{nodeId: getSelectedNodeId}}) : setAsyncActivePage({name: item.path})">
          <v-list-tile-action>
            <v-badge v-if="item.items"
                     color="'#424242'">
              <span slot="badge">
                <!-- {{ item.items.length }} -->
                <v-icon color="orange"
                        style="transform:matrix(0.8, 0, 0, 0.8, -3, 0)">list</v-icon>
              </span>
              <span>
                <v-icon class="text--lighten-1  orange--after">{{ item.icon }}</v-icon>
              </span>
            </v-badge>
            <v-badge v-else>
              <span>
                <v-icon :color="item.path === getActivePage ? 'blue' : ''">{{ item.icon }}</v-icon>
              </span>
            </v-badge>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="text--lighten-2"
                               :class="{'blue--text': item.path === getActivePage }">
              {{ item.title }}
              <!-- <div class="mybadge teal"
                   v-if="item.items">
                <span style="position: relative; left: 1px;">{{ item.items.length }}</span>
              </div> -->
            </v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-action v-if="item.items">
            <v-icon>keyboard_arrow_down</v-icon>
          </v-list-tile-action>
        </v-list-tile>
        <div v-if="item.items"
             class="node_selection_side_bar">
          <v-list-tile v-for="(subItem, j) in item.items"
                       :key="j"
                       @click="setAsyncSelectedNodeId(subItem.id); setAsyncActivePage({name: 'dashboard', params: { nodeId: subItem.id }})">
            <v-list-tile-content>
              <v-list-tile-title class="text--lighten-2"
                                 :class="{'teal--text': subItem.id == getSelectedNodeId, 'white--text': !subItem.active}">
                {{ subItem.name }}
              </v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-tooltip right
                         :color="getTooltipColor">
                <span slot="activator">
                  <span v-if="getLatestReadings">
                    <span v-if="getLatestReadings[subItem.id] && Object.keys(getLatestReadings[subItem.id]).length > 1">
                      <!-- {{getLatestReadings[subItem.id].battery}} -->
                      <span v-if="getLatestReadings[subItem.id].battery >= 80">
                        <v-icon color="green"
                                style="font-size: 15px">fa-battery-full</v-icon>
                      </span>
                      <span v-else-if="getLatestReadings[subItem.id].battery >= 50">
                        <v-icon color="yellow"
                                style="font-size: 15px">fa-battery-three-quarters</v-icon>
                      </span>
                      <span v-else-if="getLatestReadings[subItem.id].battery >= 30">
                        <v-icon color="orange"
                                style="font-size: 15px">fa-battery-half</v-icon>
                      </span>
                      <span v-else-if="getLatestReadings[subItem.id].battery >= 10">
                        <v-icon color="red"
                                style="font-size: 15px">fa-battery-quarter</v-icon>
                      </span>
                      <span v-else-if="getLatestReadings[subItem.id].battery >= 0 && getLatestReadings[subItem.id].battery !== null">
                        <v-icon color="red"
                                style="font-size: 15px">fa-battery-empty</v-icon>
                      </span>
                      <span v-else><b>---</b>&nbsp;</span>
                    </span>
                    <span v-else>
                      <v-icon color="red">fa-exclamation-circle</v-icon>
                    </span>
                  </span>
                  <span v-else>
                    <v-icon color="red">fa-exclamation-circle</v-icon>
                  </span>
                </span>
                <span>
                  <span v-if="getLatestReadings">
                    <span v-if="getLatestReadings[subItem.id] && Object.keys(getLatestReadings[subItem.id]).length > 1">
                      <span v-if="getLatestReadings[subItem.id].battery || getLatestReadings[subItem.id].battery === 0"
                            style="font-size: 16px">
                        <b>{{getLatestReadings[subItem.id].battery || 0}}%</b>
                      </span>
                      <span v-else>
                        No Battery Info
                      </span>
                    </span>
                    <span v-else>
                      Device Inactive <br> Please ReStart Device
                    </span>
                  </span>
                </span>
              </v-tooltip>
              <!-- <v-icon color="
                            green
                            "
                      style="font-size:
                            15px
                            ">fa-battery-full</v-icon>
              <v-icon color="green
                            "
                      style="font-size:
                            15px
                            ">fa-battery-full</v-icon>
              <v-icon color="green
                            "
                      style="font-size:
                            15px
                            ">fa-battery-full</v-icon> -->
            </v-list-tile-action>
          </v-list-tile>
          <v-list-tile @click="setAsyncConfigureNodeDialog(true)">
            <v-list-tile-content>
              <v-list-tile-title>
                <v-icon medium
                        class="white--text">add_circle</v-icon>
                Add Device
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </div>
      </v-list-group>
      <v-divider></v-divider>
      <!-- <v-list-tile @click="setAsyncConfigureNodeDialog(true)">
        <v-list-tile-content>
          <v-list-tile-title>Add Device</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile> -->
      <v-divider></v-divider>
      <!-- <v-list-tile @click="logout()">
        <v-list-tile-action>
          <v-icon>fa-sign-out</v-icon>
        </v-list-tile-action>
        <v-list-tile-title>Logout</v-list-tile-title>
      </v-list-tile> -->
    </v-list>
  </v-navigation-drawer>
  <v-toolbar app
             flat
             :clipped-left="mini"
             fixed
             class="primary"
             dark>
    <v-toolbar-side-icon @click.stop="drawer=!drawer"></v-toolbar-side-icon>
    <v-toolbar-title>
      {{ getActivePage | capitalize }}
      <span v-if="getActivePage==='dashboard'&&getSelectedNodeId">
        : {{ getSelectedNodeId !== 'no_node_selected' ? getNodeInfoById(getSelectedNodeId).name : 'No Node Selected' }}
      </span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <!-- <v-btn icon>
      <v-icon v-badge="{ value:'9', overlap:true }"
              class="red--after">flag</v-icon>
    </v-btn> -->
    <v-tooltip bottom
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="setAsyncWelcomeDialog(true)">
        <v-icon>info</v-icon>
      </v-btn>
      <span>Introduction</span>
    </v-tooltip>
    <!-- <v-tooltip left
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="setAsyncAccountSettingDialog(true)">
        <v-icon>settings</v-icon>
      </v-btn>
      <span>Setting</span>
    </v-tooltip> -->
    <v-tooltip bottom
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="logout()">
        <v-icon>fa-sign-out</v-icon>
      </v-btn>
      <span>Logout</span>
    </v-tooltip>
  </v-toolbar>
  <main>
    <v-content>
      <v-container fluid
                   class="border pa-0 ">
        <!-- {{getLatestReadings}} -->
        <v-layout row
                  wrap
                  class="border">
          <v-flex xs12>
            <transition name="slide-fade"
                        mode="out-in">
              <keep-alive>
                <router-view></router-view>
              </keep-alive>
            </transition>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </main>

  <configure-node></configure-node>
  <welcome></welcome>
  <account-setting></account-setting>

  <v-snackbar :top="getSnackBar.horizontalPos==='top'"
              :bottom="getSnackBar.horizontalPos==='bottom'"
              :right="getSnackBar.verticalPos==='right'"
              :left="getSnackBar.verticalPos==='left'"
              :timeout="getSnackBar.timeout"
              :color="getSnackBar.colorType"
              :multi-line="getSnackBar.mode==='multi-line'"
              :vertical="getSnackBar.mode==='vertical'"
              v-model="showSnackBar">
    <div class="subheading">{{ getSnackBar.content }}</div>
    <v-btn fab
           dark
           flat
           @click.native="showSnackBar=false">
      <v-icon>close</v-icon>
    </v-btn>
  </v-snackbar>
</v-app>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'
import configureNode from '@/components/ConfigureNode'
import welcome from '@/components/Welcome'
import accountSetting from '@/components/AccountSetting'
import * as account from '@/js/account'

export default {
  name: 'app',
  components: { configureNode, welcome, accountSetting },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      right: null,
      mini: true,
      drawer: true,
      showSnackBar: true,
      username: '',
      selectedNode: '',
      sideBarItems: [
        { title: 'Dashboard', icon: 'dashboard', path: 'dashboard' },
        { title: 'Activity', icon: 'add_alert', path: 'activity' },
        { title: 'Configuration', icon: 'search', path: 'configuration' },
        // { title: 'Settings', icon: 'settings', path: 'settings' },
        { title: 'Devices', icon: 'view_module', path: '', items: [] }
      ]
    }
  },
  created() { if (process.env.NODE_ENV !== 'production') console.log('App.vue') },
  mounted() {
    if (this.$route.name) this.setAsyncActivePage({ name: this.$route.name })
    if (this.getViewport.x < this.getMobileBreakPoint) this.setAsyncDrawer(false)
  },
  computed: {
    ...mapGetters([
      'getSelectedNodeId',
      'getActivePage',
      'getSnackBar',
      'getMini',
      'getDrawer',
      'getAccountSettingDialog',
      'getViewport',
      'getMobileBreakPoint',
      'getTooltipColor'
    ]),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getAllNodeId', 'getLatestReadings'])
  },
  methods: {
    ...mapActions([
      'setAsyncSelectedNodeId',
      'setAsyncActivePage',
      'updateAsyncSnackBar',
      'setAsyncMini',
      'setAsyncDrawer',
      'setAsyncWelcomeDialog',
      'setAsyncAccountSettingDialog',
      'setAsyncConfigureNodeDialog'
    ]),
    ...mapActions('user/node', [
      'setAsyncLatestReading'
    ]),
    logout() { account.logout() }
  },
  watch: {
    getUserData() {
      // set the node list on the side bar
      this.sideBarItems[this.sideBarItems.length - 1].items = this.getUserData.nodes
      this.username = this.getUserData.username
      if (this.getUserData.nodes.length === 0) this.setAsyncWelcomeDialog(true)
      if (this.getUserData.nodes.length !== 0) { for (let id of this.getAllNodeId) { this.setAsyncLatestReading(id) } }
      // this.setAsyncLatestReading()
    },
    getDrawer(val) { this.drawer = val },
    drawer(val) { this.setAsyncDrawer(val) },
    getMini(val) { this.mini = val },
    mini(val) { this.setAsyncMini(val) },
    showSnackBar(val) { this.updateAsyncSnackBar({ show: val }) },
    getSnackBar(val) { this.showSnackBar = val.show }
  }
}
</script>

<style lang="stylus">
  //This will apply on entire environ
  @import '../node_modules/vuetify/src/stylus/settings/_colors'
  $theme := {
    primary: $blue.lighten-2
    accent: $blue-grey.darken-1
    secondary: $grey.darken-4
    info: $blue.lighten-1
    warning: $amber.darken-2
    error: $red.accent-3
    success: $green.lighten-2
  }
  @import '../node_modules/vuetify/src/stylus/main'
</style>

<style lang="sass">
@import url('https://fonts.googleapis.com/css?family=Quicksand')
html, body
  margin: 0
  cursor: url('./assets/cursor.png')

.border
  // border: 1px dashed grey

.node_selection_side_bar
    height: 300px
    overflow: hidden
    overflow-y: scroll

.mybadge
    display: inline-block
    padding-right: 2px
    width: 22px
    height: 22px
    border-radius: 50px
    color: #fff
    text-align: center

#app
  font-family: Quicksand, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  color: #2c3e50

#mainContainer
  // margin-top: $topbar-height
  // margin-left: $leftbar-width
  // width: calc(100% - $leftbar-width)
  padding-top: 0px

.font23
    font-size: 23px

.slide-fade-enter
  transform: translateY(1300px)

.slide-fade-enter-active
  transition: all 0.8s ease

.slide-fade-enter-to
  transform: translateY(0px)

.slide-fade-leave
  transform: translateX(0px)

.slide-fade-leave-active
  transition: all 0.2s

.slide-fade-leave-to
  transform: translateX(2500px)
</style>



