<template>
<v-card class="white lighten-5 border mb-1">
  <v-card-text class="sensorBox border pt-1 px-0">
    <v-layout row
              wrap
              class="border">
      <v-flex xs12
              class="border text-xs-left subheading pa-0">
        <v-layout row
                  wrap
                  class="border">
          <v-flex xs8
                  class="border px-2">
            <SensorAvatar :sensor="sensorType"
                          :size="'20px'"></SensorAvatar>
            {{displayName | capitalize}}
          </v-flex>
          <v-flex xs4>
            <v-layout row
                      wrap
                      class="text-xs-right">
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border px-2">
                <v-icon class="border"
                        style="font-size: 20px;"
                        :class="healthColorClass">
                  radio_button_checked
                </v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex xs12
              class="border display-1 grey--text text-xs-center headline">
        <!-- {{latestReading}} -->
        <span v-if="latestReading == 'no_data_found'">
          No Data
        </span>
        <span v-else-if="latestReading == '_loading'">
          <v-progress-circular indeterminate
                               v-bind:size="28"
                               :color="getSensorOptions[sensorType].colorName"></v-progress-circular>
        </span>
        <span v-else>
          <i-odometer class="iOdometer"
                      :value="latestReading"></i-odometer>
          <span v-html="unit"
                style="position:relative; top: 4px;"></span>
        </span>
      </v-flex>
      <v-flex xs12
              class="border pa-0">
        <div v-if="!isEditing"
             class="infoBox pa-0 grey lighten-5 border">
          <v-layout row
                    wrap
                    class="border innerInfoBox pa-0"
                    :class="healthColorClass">
            <v-flex xs12
                    class="subheading text-xs-left border pa-0 ml-4"
                    style="font-weight: 600">Ideal</v-flex>
            <v-flex xs10
                    offset-xs1
                    class="title text-xs-center border"
                    style="font-weight: 600">
              <i-odometer class="iOdometer"
                          :value="parseInt(idealMin)"></i-odometer>
              -
              <i-odometer class="iOdometer"
                          :value="parseInt(idealMax)"></i-odometer>
              <span v-html="unit"
                    style="position:relative; top: 2px;"></span>
            </v-flex>
            <v-flex xs1>
              <v-icon class="icon"
                      @click.stop="isEditing = true">edit</v-icon>
            </v-flex>
          </v-layout>
        </div>
        <div v-else
             class="px-2 infoBox grey lighten-5 border">
          <v-layout>
            <v-flex xs5
                    class="px-1">
              <v-text-field name="Min"
                            label="Min"
                            v-model="editMin"></v-text-field>
            </v-flex>
            <v-flex xs5
                    class="px-1">
              <v-text-field name="Max"
                            label="Max"
                            v-model="editMax"></v-text-field>
            </v-flex>
            <v-flex xs2
                    class="pt-1 body-1">
              <div class="indigo lighten-4 white--text text-xs-center edit-btn mb-1"
                   @click.stop="isEditing = false; updateOneNodeInfo()">Save</div>
              <div class="blue lighten-4 white--text text-xs-center edit-btn mb-1"
                   @click.stop="isEditing = false">Cancel</div>
            </v-flex>
          </v-layout>
        </div>
      </v-flex>
    </v-layout>
  </v-card-text>
</v-card>
</template>

<script>
import IOdometer from 'vue-odometer'
import SensorAvatar from '@/components/SensorAvatar.vue'
import { mapGetters, mapActions } from 'vuex'
import { dbSensorName } from '@/js/transforms.js'

export default {
  name: 'sensorBox',
  components: { IOdometer, SensorAvatar },
  props: {
    nodeId: { type: String, required: true },
    sensorType: { type: String, default: 'Loading' }
  },
  data() {
    return {
      isEditing: false,
      editMin: '',
      editMax: ''
    }
  },
  created() {},
  watch: {
    isEditing() {
      this.editMin = this.idealMin
      this.editMax = this.idealMax
    }
  },
  computed: {
    ...mapGetters(['getSelectedNodeId', 'getTooltipColor']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getSensorOptions', 'getLatestReadings', 'getLatestReadingById']),
    ...mapGetters('user/notification', ['getNotificationsByNode']),
    selectedNode() { return this.getNodeInfoById(this.nodeId) },
    unit() { return this.getSensorOptions[this.sensorType].unit },
    displayName() { return this.getSensorOptions[this.sensorType].name },
    idealMin() { return this.selectedNode[`${dbSensorName(this.sensorType)}Min`] },
    idealMax() { return this.selectedNode[`${dbSensorName(this.sensorType)}Max`] },
    isHealthy() { return (this.latestReading <= this.idealMax) && (this.latestReading >= this.idealMin) },
    healthColorClass() {
      if (this.latestReading === '_loading') return 'grey--text'
      else if (this.isHealthy) return 'light-blue--text'
      else return 'pink--text'
    },
    latestReading() {
      if (!this.getLatestReadings) return '_loading'
      else if (!(this.nodeId in this.getLatestReadings)) return '_loading'
      else if (this.getLatestReadings[this.nodeId][this.sensorType] === null) return 'no_data_found'
      else if (!(this.sensorType in this.getLatestReadings[this.nodeId])) return 'no_data_found'
      else return this.getLatestReadings[this.nodeId][this.sensorType]
    }
  },
  methods: {
    ...mapActions('user/node', ['updateAsyncNodesInfo']),
    updateOneNodeInfo() {
      this.updateAsyncNodesInfo({
        nodes: [this.nodeId],
        value: {
          [`${dbSensorName(this.sensorType)}Min`]: this.editMin,
          [`${dbSensorName(this.sensorType)}Max`]: this.editMax
        }
      })
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped lang="sass">
.border
  // border: 1px dashed grey

.infoBox
  cursor: default
  height: 55px
  margin: 0 auto
  width: 98%
  .innerInfoBox
    transition: all 0.1s ease

.icon
  padding-left: 0
  transition: all 0.4s ease
  cursor: default
  font-size: 20px
  &:hover
    color: #55dd97
    cursor: pointer

.nodeId
  cursor: default
  font-weight: 600
  color: #bEbEbE

.sensorBox
  cursor: default
  height: 120px
  box-shadow: 0px 1px 1px #BBBBBB
  transition: all 0.5s ease
  &:hover
    box-shadow: 3px 3px 4px #888888

.edit-btn
  border-radius: 2px
  transition: all 0.3s ease
  cursor: pointer
  &:hover
    box-shadow: 0px 2px 3px #a7a7a7

</style>



